<template>

    <div class="synth-container">

        <!--Master Pitch Container -->
        <div class="module master-pitch-container">

            <!-- Octave LED Meter -->
            <div 
                title="octave meter"
                class="led-container">

                <led 
                diameter="15%"
                v-for="led in ui.leds" 
                v-bind:color="led.color" 
                v-bind:currentOctave="store.config.currentPreset.octave" 
                v-bind:octave="led.octave">
                </led>

            </div>

            <!-- Octave -->
            <div 
            title="octave"
            class="octave-buttons-container">

                <btn 
                title="octave +1"
                class="octave-button-up" 
                v-on:press="adjustOctave(+1)">

                    <i 
                    v-bind:style="{ color: palette.white }" 
                    class="fa fa-caret-up" 
                    slot="label">
                    </i>

                </btn>
                <btn 
                title="octave -1"
                class="octave-button-down" 
                v-on:press="adjustOctave(-1)">

                    <i 
                    v-bind:style="{ color: palette.white }" 
                    class="fa fa-caret-down" 
                    slot="label">
                    </i>

                </btn>
            </div>
        </div>

        <!-- Master Volume Knob Container -->
        <div 
            title="Master Volume. Swipe up to increase. Swipe down to decrease. Tap to mute."
            class="module master-volume-container">

            <!-- Master LED Readings --> 
            <div class="volume-container">
                <led color="red"></led>
                <led color="red"></led>
                <led color="red"></led>
                <led color="red"></led>
                <led color="red"></led>
            </div>

            <!-- Master Volume Knob -->
            <knob
            v-on:toggle="toggleMasterVol"
            color="black" 
            diameter="50%">
            </knob>

        </div>

        <!-- Waveforms -->
        <div class="module waveforms-and-options-container">

            <div class="waveforms-container">
                <knob 
                v-for="waveform in ui.waveforms" 
                v-on:toggle="toggleOscillatorVol"
                v-bind:active="store.config.currentPreset.oscillators[ waveform.name ].active"
                v-bind:bg-image-active="waveform.knob.img.active"
                v-bind:bg-image-inactive="waveform.knob.img.inactive"
                v-bind:waveform="waveform.name"
                v-bind:color="waveform.knob.color"
                v-bind:title="`toggle ${ waveform.name } level on / off.`"
                diameter="25%">
                </knob>
            </div>

            <div class="waveform-level-container">
                <slider 
                v-for="waveform in ui.waveforms" 
                v-on:slide="adjustOscillatorVolume"
                v-bind:control-source="soundEngine.settings.oscillators[ waveform.name ]"
                v-bind:direction="waveform.slider.direction"
                v-bind:name="waveform.name"
                v-bind:content="waveform.name"
                v-bind:title="`adjust ${ waveform.name }wave volume.`"
                v-bind:color="waveform.slider.color">
                </slider>
            </div>

        </div>

        <!-- Envelope -->
        <div class="module envelope-container">

            <div 
                v-for="param in ui.envelope"
                class="envelope-slider-container">

                <h1 
                v-bind:title="param.name"
                class="envelope-slider-label">{{ param.name | abbreviate }}</h1>

                <slider 
                v-on:slide="setEnvelopeValue"
                v-bind:control-source="soundEngine.settings.envelope[ param.name ]"
                v-bind:direction="param.slider.direction"
                v-bind:opacity="0.4"
                v-bind:name="param.name"
                v-bind:content="param.name"
                v-bind:color="param.slider.color"
                v-bind:title="param.slider.description">
                </slider>
            </div>

        </div>

        <!-- Modulation Wheels Container -->
        <div class="module wheels-container">
        </div>

        <!-- Preset Settings -->
        <div class="module selector-container">
            <preset-manager 
            v-on:NEW_PRESET="newPreset"
            v-on:CHANGE_PRESET="changePreset"
            v-on:UPDATE_PRESET="updatePreset"
            v-bind:preset-data="store.config">
            </preset-manager>
        </div>

        <!-- Signature Sign Container -->
        <div class="module title-container">
            <h1 class="make">ARS</h1>
            <h1 class="model">1</h1>
        </div>

        <!-- Keyboard Container -->
        <div class="module keyboard-container">
            <div class="keyboard">
                <key 
                v-for="index in 25" 
                v-bind:index="index - 1" 
                v-bind:blackKeys="ui.keyboard.blackKeys"
                v-on:noteon="noteOn"
                v-on:noteoff="noteOff">
                </key>
            </div>
        </div>

    </div>
</template>

<style lang="scss">

html, body, * {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}
html, body {
    height: 100%;
    font-family: Arial, Helvetica;
}
h1 {
    color: black;
    user-select: none;
}
.synth-container {
    height: 100%;
    width: 100%;
    font-size: 0em;
}
.synth-container > div > * {
    font-size: initial;
}
.controls-upper,
.controls-lower {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 30%;
}

.module,
.master-volume-container,
.master-pitch-container,
.waveforms-and-options-container,
.filter-container,
.sequencer-container,
.wheels-container,
.envelope-container,
.sequencer-container,
.title-container,
.lfo-container,
.sequencer-container {
    min-width: calc(100% / 2);
    width: 25%;
    height: 30%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align:top;
}
.title-container {
    font-weight: 100;
    font-family: Helvetica Neue;
}
.envelope-slider-container { 
    border-right: 1px solid rgb(239,239,239);
    position: relative;
    width: 25%;
    height: 100%;
}
h1.envelope-slider-label {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: black;
}
h1.make,
h1.model {
    margin-left: 10px;
    color: black;
    font-size: 5vw;
}
h1.model {
    margin-left: 34px;
    margin-top: -16px;
    font-size: 200px;
}
h1.make {
    font-weight: 100;
}
.wheels-container {
    background: aquamarine;
}
.master-volume-container {
    background: pink;
}
.octave-button-up,
.octave-button-down {
    display: block;
    font-size: 4em;
}

.master-pitch-container {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background: rgb(40,40,40);
}
.led-container,
.volume-container {
    padding: 5%;
    height: 100%;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;

}
.octave-buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    vertical-align: bottom;
    width: 50%;
    height: 100%;
}
.waveforms-and-options-container {
    background: lightgreen;
    flex-direction: column;
}
.waveforms-container,
.waveform-level-container {
    display: inline-flex;
    height: 50%;
    width: 100%;
}

.waveform-level-container {
    position: relative;
    background: whitesmoke;
}
.filter-container {
    background: #e8e8e8;
}
.envelope-container {
    background: whitesmoke;
}
.lfo-container {
    background: lightblue;
}
.sequencer-container {
    background: yellow;
    width: 100%;
}
.keyboard-container {
    height: 40%;
    width: 100%;
}

.keyboard {
    height: 100%;
    width: 100%;
    background: whitesmoke;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
}

.knob,
.btn {
    box-shadow: 0 2px 0px 0px #afafaf;
}

@media(max-width: 500px) {
    .led-container {
        flex-direction: column;
    }
    .keyboard-container {
        transform: rotate(180deg);
        width: 100%;
        height: 150%;
    }
    .keyboard {
        flex-direction: column;
        height: 100%;
    }
}

</style>

<script>

    import FastClick from 'fastclick';
    import Key from './Key';
    import Sequencer from './Sequencer';
    import Slider from './Slider';
    import Knob from './Knob';
    import Wheel from './Wheel';
    import Btn from './Btn';
    import Led from './Led';
    import PresetManager from './PresetManager';

    import SoundEngine from './../services/SoundEngine';
    import { synthConfig } from './../config/synth'; 
    import { getScreenWidth }  from './../utils';
    import palette from './../services/colorPalette';
    import Store from './../services/localStorage';

    const { ui, params } = synthConfig;
    const store = new Store({ defaults: params });
    const soundEngine = new SoundEngine();

    export default {

        data: function() {
            return {
                ui,
                palette,
                store,
                soundEngine,
                preset: null
            };
        },
        created: function() {
            this.soundEngine.settings = this.store.config.currentPreset;
        },
        mounted: function() {
            document.addEventListener('load', () => { FastClick.attach(document.body); }); 
        },
        methods: {
            toggleMasterVol() {
                this.soundEngine.active = !this.soundEngine.active;
            },
            toggleOscillatorVol({ waveform }) {
                this.soundEngine.settings.oscillators[waveform].active = !this.soundEngine.settings.oscillators[waveform].active; 
            },
            adjustOctave(direction) { 
                this.soundEngine.octave += direction;
            },
            adjustOscillatorVolume({ name, value }) {
                this.soundEngine.settings.oscillators[ name ].value = value;
            },
            setEnvelopeValue({ name, value }) {
                this.soundEngine.settings.envelope[ name ].value = value;
            },
            noteOn({ index }) {
                this.soundEngine.playNote(index);
            },
            noteOff({ index }) {
                this.soundEngine.noteOff();
            },
            updatePreset({ name }) {
                const settings = this.store.config.currentPreset;
                this.store.updatePreset(name, settings);
            },
            changePreset({ name }) {
                console.log('change preset');
                this.store.config.currentPresetName = name;
                this.store.config.currentPreset = this.store.config.presets[ name ];
            },
            newPreset({ name }) {
                const settings = this.store.config.currentPreset;
                this.store.addPreset(name, settings); 
            }
        },
        watch: {
            // when preset changes, rebind it to the sound engine settings
            preset: function() {
                this.soundEngine.settings = this.preset;
            }
        },
        computed: {
            preset: function() {
                return this.store.config.currentPreset;
            }
        },
        filters: {
            abbreviate: s => s.charAt(0).toUpperCase(),
        },
        components: { 
            Btn,
            Key, 
            Knob,
            Led,
            Sequencer, 
            PresetManager,
            Slider, 
            Wheel
        }
    };

</script>



